html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-size: 1.4rem;
  font-family: "Nunito Sans", sans-serif;
  height: 100%;
  background-color: hsl(0deg, 0%, 98%);
}
@media (min-width: 768px) {
  body {
    font-size: 1.6rem;
  }
}
body.dark {
  background-color: hsl(207deg, 26%, 17%);
  color: hsl(0deg, 0%, 100%);
}
body.dark header,
body.dark input,
body.dark button,
body.dark select {
  background-color: hsl(209deg, 23%, 22%);
  color: hsl(0deg, 0%, 100%);
}
body.dark .country {
  background-color: hsl(209deg, 23%, 22%);
}
body.dark .sun {
  fill: hsl(0deg, 0%, 100%);
}

button {
  background-color: inherit;
  border: none;
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
}

input {
  font-family: inherit;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  width: min(95%, 140rem);
  margin-inline: auto;
}

.section {
  margin-top: 5rem;
}

.selected-country.inactive,
.countries.inactive,
.filter__select.inactive,
.filter__form.inactive {
  display: none;
}

.back {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding: 1rem 2rem;
  margin-block: 2rem;
  font-weight: 600;
}

.world {
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0rem 0.5rem 1rem rgba(0, 0, 0, 0.1);
  padding-block: 2rem;
}
.world__container {
  display: flex;
  justify-content: space-between;
}
.world__heading {
  cursor: pointer;
  font-size: 1.7rem;
}

.theme {
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}
.theme-container {
  cursor: pointer;
  border: none;
}
.theme .sun,
.theme .moon {
  position: absolute;
  left: -2.5rem;
  top: 50%;
  transform: translateY(-50%);
}
.theme .sun.off {
  display: none;
}
.theme .moon.off {
  display: none;
}
.theme-text {
  font-weight: 600;
}

.filter {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .filter__form {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.filter__space {
  display: flex;
  box-shadow: 0rem 0.5rem 1rem rgba(0, 0, 0, 0.1);
  width: 100%;
}
@media (min-width: 768px) {
  .filter__space {
    width: 50%;
  }
}
.filter__icon {
  padding: 0 2rem;
  background-color: hsl(0deg, 0%, 100%);
  color: hsl(0deg, 0%, 52%);
}
.filter #search {
  width: 100%;
  border: none;
  padding: 2rem;
  font-size: 1.8rem;
}
.filter #search:placeholder {
  font-size: inherit;
}
.filter #search:focus {
  outline: none;
}
.filter__country {
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .filter__country {
    margin-top: 0;
  }
}
.filter__select {
  box-shadow: 0rem 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border: none;
  padding: 2rem 3rem;
  font-size: 1.6rem;
}
.filter__select:focus {
  outline: none;
}

.countries {
  width: min(80%, 140rem);
  margin-block: 10rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .countries {
    width: min(95%, 140rem);
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
  }
}
@media (min-width: 1440px) {
  .countries {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 6rem;
  }
}
.countries:hover .country:not(:hover) {
  transform: scale(0.99);
}

.country {
  cursor: pointer;
  border-radius: 1rem;
  overflow: hidden;
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 1rem 1.2rem 2rem rgba(0, 0, 0, 0.2);
  transition: transform 450ms;
}
.country__img img {
  width: 100%;
}
.country:hover {
  transform: scale(1.05) translateY(-0.5rem);
}
.country:active {
  transform: scale(0.99);
  box-shadow: 1rem 1.2rem 1.5rem rgba(0, 0, 0, 0.2);
}
.country:hover:not(:hover) {
  transform: scale(0.95);
}
.country__content {
  padding: 3rem 2rem;
}
.country__name {
  margin-block-end: 2rem;
}
.country__li {
  margin-bottom: 1rem;
}
.country__li span {
  font-weight: 800;
}

.selected {
  margin-top: 3rem;
}
.selected__image-container img {
  width: 80%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 768px) {
  .selected {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 15rem;
    align-items: center;
  }
}
.selected__box {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1rem;
}
.selected__name {
  font-size: 3rem;
  margin-block: 1.5rem;
}
.selected__content {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .selected__content {
    flex-direction: row;
    justify-content: space-around;
  }
}
.selected__li {
  margin-bottom: 1rem;
}
.selected__li span {
  font-weight: 600;
}
.selected__borders {
  margin-top: 3rem;
}
.selected__paragraph {
  font-weight: 600;
  margin-block: 1.5rem;
}
.selected__borders {
  margin-bottom: 2rem;
}

.borders {
  display: flex;
  gap: 2rem;
}
.borders__border {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  padding-inline: 1.5rem;
  padding-block: 0.8rem;
  cursor: pointer;
}